<template>
  <div class="groups-container">
    <div style="display: flex; justify-content: flex-end;">
      <div class="add-new-group-button" @click="$router.push('/new-group')">
        + New Group
      </div>
    </div>

    <table class="group-table">
      <thead>
        <tr>
          <th class="th-group-name">
            Group Name
          </th>
          <th class="th-computer-count">Computer Count</th>
          <th class="th-current-message">Current Message</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="group in list"
          :key="group._id"
          @click="moveToEditGroup(group)"
        >
          <td>{{ group.name }}</td>
          <td>{{ group.computers.length }}</td>
          <td>{{ group.messageCount }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  methods: {
    moveToEditGroup(group) {
      this.$router.push("/edit-group/" + group._id);
    },
  },
  mounted() {
    this.$store.dispatch("group/loadList", { notify: this.$notify });
  },
  computed: {
    ...mapState("group", ["list"]),
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/groups.scss";
</style>
